.step__item {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.step__number {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 65px;
    height: 65px;
    border: 2px solid #bdbaba;
    background: #71468a;
    font-weight: normal;
    font-size: 39px;
    line-height: 46px;
    color: #ffffff;
    border-radius: 50%;
    margin-bottom: 19px;
}

.step__content {
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    color: #ffffff;
}