

.calculator__body {
    margin-top: 78px;
}

.body__title {
    color: #ffffff;
    font-size: 26px;
}

.body__picture-wrapper {
    position: relative;
    top: 20px;
}

body {
	background: linear-gradient(#3e1366, #641fa4);
   background-size: 100% 300%;
	animation: gradient 10s ease infinite;
}

@keyframes gradient {
	0% {
		background-position: 0% 0%;
	}
	50% {
		background-position: 00% 50%;
	}
	100% {
		background-position: 0% 00%;
	}
}

p {
    float:right;
    color:#fff;
    padding-bottom: 20px;
    margin-right:15px;
    opacity:.5
}
