* {
    font-family: "Roboto", sans-serif;
}

body {
    background: linear-gradient(180deg, #2e0047 0%, rgba(46, 0, 71, 0.71) 100%);
    background-blend-mode: multiply;
    min-height: 100vh;
}

p {
    float: right;
    color: #fff;
    margin-top: 35px;
    margin-right: 15px;
    opacity: .5;
}

.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}

.d-flex {
    display: flex;
}

.row {
    display: flex;
}

.text-center {
    text-align: center;
}

.justify-content-between {
    justify-content: space-between;
}

.align-items-center {
    align-items: center;
}